<template>
<div class="container">
    <div class="row">
        <h3 class="font-heavy">Wonders</h3>
    </div>
    <div class="row">
        <div class="col-12">
            <blockquote>
                Là những thẻ bài màu <span class='violet'>tím</span>. 
                Chúng tượng trưng cho những kỳ quan. Mỗi thẻ sẽ mang lại cho người chơi những khả năng đặc biệt.
            </blockquote>
        </div>
    </div>
    <br>
    <br>
    <div class="row">
        <input v-model.trim='search_name' type="text" placeholder="Tìm kiếm kỳ quan">
    </div>
    <div class="row">
        <transition-group enter-active-class="animated" enter-to-class="fadeInLeft" leave-active-class='animated' leave-to-class="fadeOutLeft" tag='ul' name='wonders'>
            <li class='wonders-item' v-for="w in wonders_list" v-bind:key="w.name">
                <span class='wonders-name font-heavy'>{{w.name}}</span>: {{w.ability}} 
            </li>
        </transition-group>
    </div>
</div>
</template>
<style lang="css" scoped>

.wonders-move{
    transition: all 1s;
}

span.violet{
    color: #8e44ad;
}
span.wonders-name{
    color: #8e44ad;
}

blockquote{
    border-style:solid;
    margin: 0 0 0 0;
    border-width: 0 0 0 0;
    border-left-width: 3px;
    padding-left: 9px;
    border-left-color: #2c3e50;
}
input{
    padding-left:4px;
    width: 78%;
    border-radius: 4px;
    border-color: black;
    border-style: solid;
    border-width: 2px 2px 2px 2px;
    transition: border-color .4s;
}
input:focus{
    border-color: #2980b9;
}

li{
    margin-top: 5px;
    margin-bottom: 5px;
}
</style>
<script>
    var wonders = require('../assets/json_data/wonder.json')['wonders']
    export default{
        
        data: function(){
            return {
                search_name: '',
                wonders    
            }
        },
        computed:{
           wonders_list() {
               if (this.search_name === '') return this.wonders 
               return _.filter(this.wonders, (wonder) => wonder.name.toLowerCase().indexOf(this.search_name.toLowerCase()) !== -1)
           }
        }
    }
</script>


