<style lang="css" scoped>
.leader-link2{
  color: #8e44ad;
}
.leader-link3{
  color: #f1c40f;
}
.leader-link4{
  color: #27ae60;
}
#sub-title{
  color: #bdc3c7;
}
a{
  color:#2ecc71;
}

#main-title{
  -moz-user-select: none;
  user-select: none;
  -ms-user-select: none;
}



</style>
<script>
  export default{
    methods : {
      go_home : function() { 
        this.$router.push("/")
      }
    }
  }
</script>


