import _ from 'lodash'
import '../assets/animate-css/animate.css'
import Vue from 'vue'
import Router from 'vue-router'
import Leader from '../components/leaders.vue'
import Wonder from '../components/wonders.vue'
import Action from '../components/actions.vue'
import home from '../components/index.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/leaders',
      component: Leader 
    },
    {
      path: '/wonders',
      component: Wonder
    },
    {
      path: '/actions',
      component: Action  
    },
    {
      path: '/',
      component: home
    }
  ]
})



