<template>
<div class="container">
    <div class="row">
        <h3 class="font-heavy">Actions</h3>
    </div>
    <div class="row">
        <div class="col-12">
            <blockquote>
                Những thẻ bài màu <span class='yellow'>vàng</span>. 
                Là những hành động có hiệu ứng 1 lần. Mỗi thẻ sẽ mang lại cho người chơi những lợi thế tức thì.
            </blockquote>
        </div>
    </div>
    <br>
    <br>
    <div class="row">
        <input v-model.trim='search_name' type="text" placeholder="Tìm kiếm hành động">
    </div>
    <div class="row">
        <transition-group enter-active-class="animated" enter-to-class="fadeInLeft" leave-active-class='animated' leave-to-class="fadeOutLeft" tag='ul' name='actions'>
            <li class='action-item' v-for="a in actions_list" v-bind:key="a.name">
                <span class='action-name font-heavy'>{{a.name}}</span>: {{a.effect}} 
            </li>
        </transition-group>
    </div>
</div>
</template>
<style lang="css" scoped>

.actions-move{
    transition: all 1s;
}

span.yellow{
    color: #f1c40f;
}
span.action-name{
    color: #f1c40f;
}

blockquote{
    border-style:solid;
    margin: 0 0 0 0;
    border-width: 0 0 0 0;
    border-left-width: 3px;
    padding-left: 9px;
    border-left-color: #2c3e50;
}
input{
    padding-left:4px;
    width: 78%;
    border-radius: 4px;
    border-color: black;
    border-style: solid;
    border-width: 2px 2px 2px 2px;
    transition: border-color .4s;
}
input:focus{
    border-color: #2980b9;
}

li{
    margin-top: 5px;
    margin-bottom: 5px;
}
</style>
<script>
    var actions = require('../assets/json_data/action.json')['actions']
    export default{
        
        data: function(){
            return {
                search_name: '',
                actions    
            }
        },
        computed:{
           actions_list() {
               if (this.search_name === '') return this.actions 
               return _.filter(this.actions, (action) => action.name.toLowerCase().indexOf(this.search_name.toLowerCase()) !== -1)
           }
        }
    }
</script>


